

label {
  display: block;
  background: #f00;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  position: absolute;
  top: 0%;
  left: 0%;
  margin-left: 0px;
  margin-top: 0px;
  transition: all .5s;
}
.text {
    text-align: center;
    margin-top: 40%;
}
input:checked + label {
  width: 100%;
  height: 100%;
  top: 0;
  margin-top: 0;
  left: 0;
  margin-left: 0;
  border-radius: 0;
  background: #0f0;
}

input {
 visibility: hidden;
 position: absolute;
}